import { useMemo } from 'react'
import TimeMark from 'components/molecules/TimeMark'
import * as S from './styled'
import useReportContext from 'context/reportContext'


const Timeline = () => {

  const {fightData = {}} = useReportContext()
  const {duration} = fightData
  const [TimeMarks, Lines] = useMemo(() => {
    const minutePercentLength = 60 / (duration / 1000) * 100
    const minutesToShow = Math.floor((duration / 1000) / 60)

    const elements = []
    const lines = []

    for (let i = 0; i <= minutesToShow; i++ ) {
      elements.push(
        <S.TimeMarkPositioner
          key={i}
          style={{
            '--left-pos': minutePercentLength * i + '%'
          }}
        >
          <TimeMark time={i} key={i} />
        </S.TimeMarkPositioner>
      )
      lines.push(
        <S.Line
          key={i}
          style={{
            '--left-pos': minutePercentLength * i + '%'
          }}
        />
      )
    }

    return [elements, lines]

  }, [duration])

  return (
    <>
    <S.Marks>
      <S.Sizer>
        <TimeMark time={1} />
      </S.Sizer>
      {TimeMarks}
    </S.Marks>
    {Lines}
    </>
  )
}


export default Timeline