import { useCallback, useEffect, useMemo, useRef, useState, useReducer } from 'react'
import Ability from 'components/molecules/Ability'
import * as S from './styled'
import _throttle from 'lodash/throttle'

function abilityCastTimesInitializer({
  cooldown,
  totalTime,
}) {

  const initialAbilityCastTimes = []
  let currentTime = 0

  while (currentTime < totalTime) {
    initialAbilityCastTimes.push(currentTime)
    currentTime = currentTime + cooldown
  }

  return initialAbilityCastTimes
}

function abilityCastTimesReducer(abilityCastTimes, action) {

  const {
    type,
    isShiftPressed,
    isCtrlPressed,
    timeShift,
    grabbedAbility,
    cooldown,
    totalTime,
  } = action

  if (type === 'moveInstance') {

    const nextAbilityCastTimes = [...abilityCastTimes]
    nextAbilityCastTimes[grabbedAbility] += timeShift

    if (isCtrlPressed) {
      for (let i = 0; i < nextAbilityCastTimes.length; i++) {
        if (i === grabbedAbility) continue
        nextAbilityCastTimes[i] += timeShift
      }
    }

    if (isShiftPressed) {
      for (let i = grabbedAbility + 1; i < nextAbilityCastTimes.length; i++) {
        nextAbilityCastTimes[i] = nextAbilityCastTimes[i - 1] + cooldown
      }
    }

    // Move previous casts only if they overlap
    for (let i = grabbedAbility - 1; i >= 0; i--) {
      if (nextAbilityCastTimes[i + 1] - nextAbilityCastTimes[i] < cooldown) {
        nextAbilityCastTimes[i] = nextAbilityCastTimes[i + 1] - cooldown
      }
    }

    // Correct if out of left bound
    if (nextAbilityCastTimes[0] < 0) {
      const overflow = -nextAbilityCastTimes[0]
      for (let i = 0; i <= grabbedAbility; i++) {
        nextAbilityCastTimes[i] += overflow
      }
    }

    // Move next casts only if they overlap
    for (let i = grabbedAbility + 1; i < nextAbilityCastTimes.length; i++) {
      if (nextAbilityCastTimes[i] - nextAbilityCastTimes[i - 1] < cooldown) {
        nextAbilityCastTimes[i] = nextAbilityCastTimes[i - 1] + cooldown
      }
    }

    // Remove casts out of right bound
    while (nextAbilityCastTimes[nextAbilityCastTimes.length - 1] > totalTime) {
      nextAbilityCastTimes.pop()
    }

    // Add casts if there's space for them
    while (nextAbilityCastTimes[nextAbilityCastTimes.length - 1] + cooldown <= totalTime) {
      nextAbilityCastTimes.push(nextAbilityCastTimes[nextAbilityCastTimes.length - 1] + cooldown)
    }

    if (!nextAbilityCastTimes.length) {
      nextAbilityCastTimes.push(totalTime)
    }

    return nextAbilityCastTimes
  }

  throw new Error('Undefined Action')
}

const AbilityBar = ({
  ability,
  totalTime,
  panelWidth,
}) => {

  const {
    cooldown,
  } = ability

  const [abilityCastTimes, dispatch] = useReducer(abilityCastTimesReducer, {
    cooldown,
    totalTime,
  }, abilityCastTimesInitializer)

  const [grabbedAbility, setGrabbedAbility] = useState(null)

  const secondsPerPixel = useMemo(() => {
    return totalTime / panelWidth
  }, [panelWidth, totalTime])


  const mouseXRef = useRef(null)

  const handleMouseMove = useCallback(e => {
    const mouseX = e.pageX
    const lastX = mouseXRef.current
    mouseXRef.current = mouseX
    const distance = mouseX - lastX
    const isShiftPressed = e.shiftKey
    const isCtrlPressed = e.ctrlKey

    dispatch({
      type: 'moveInstance',
      isShiftPressed,
      isCtrlPressed,
      timeShift: distance * secondsPerPixel,
      grabbedAbility: grabbedAbility,
      cooldown,
      totalTime,
    })
  }, [secondsPerPixel, grabbedAbility, cooldown, totalTime])


  const handleMouseUp = useCallback(() => {
    setGrabbedAbility(null)
  }, [])

  const throttledHandleMouseMove = useMemo(() => {
    return _throttle(handleMouseMove, 20)
  }, [handleMouseMove])

  const handleMouseDown = useCallback((e, abilityIndex) => {
    mouseXRef.current = e.pageX
    setGrabbedAbility(abilityIndex)
  }, [])

  useEffect(() => {
    if (grabbedAbility !== null) {
      window.addEventListener('mouseup', handleMouseUp)
      window.addEventListener('mousemove', throttledHandleMouseMove)
    }

    return () => {
      window.removeEventListener('mouseup', handleMouseUp)
      window.removeEventListener('mousemove', throttledHandleMouseMove)
    }
  }, [throttledHandleMouseMove, handleMouseUp, grabbedAbility])


  const AbilityInstances = useMemo(() => {
    return abilityCastTimes.map((instanceTime, i) => {

      const percentLeft = instanceTime / totalTime * 100

      return (
        <S.InstancePositioner
          key={i}
          onMouseDown={(e) => handleMouseDown(e, i)}
          style={{
            '--left-pos': percentLeft + '%',
            '--cursor': grabbedAbility !== null ? 'grabbing' : 'grab',
          }}
        >
          <Ability ability={ability} />
        </S.InstancePositioner>
      )
    })
  }, [ability, abilityCastTimes, totalTime, grabbedAbility, handleMouseDown])


  return (
    <S.Root style={{
      '--cursor': grabbedAbility !== null ? 'grabbing' : 'default',
    }}>
      <S.DummySizer>
        <Ability ability={ability} />
      </S.DummySizer>
      {AbilityInstances}
    </S.Root>
  )
}


export default AbilityBar