import useMouseMove from "hooks/useMouseMove"
import * as S from './styled'
import _padStart from 'lodash/padStart'
import usePanelContext from "context/panelContext"
import useReportContext from "context/reportContext"
import { useRef, useCallback } from "react"


const InfoBar = () => {

  const {fightData = {}, setCustomDuration} = useReportContext()
  const {duration} = fightData
  const {width, panelElement} = usePanelContext()
  const [x] = useMouseMove(panelElement)

  const hoverTime = x * duration / width
  const minutes = hoverTime ? parseInt((hoverTime / 1000) / 60) : '--'
  const seconds = hoverTime ? _padStart(parseInt((hoverTime / 1000) % 60), 2, '0') : '--'

  const inputRef = useRef()

  const handleClick = useCallback(() => {
    const timeString = inputRef.current.value
    const [minutes, seconds] = timeString.split(":")
    const millis = (parseInt(minutes) * 60 + parseInt(seconds)) * 1000
    setCustomDuration(millis)
  }, [setCustomDuration])

  return (
    <S.Root>
      <div>
        Time: {minutes}:{seconds}
      </div>
      <div>
        <input ref={inputRef} />
        <button onClick={handleClick}>go</button>
      </div>
    </S.Root>
  )
}


export default InfoBar
