import useReportContext from 'context/reportContext'
import { useEffect, useState } from 'react'
import EventBar from '../EventBar'
import * as S from './styled'

const colors = [
  "#FF6A39",
  "#00B388",
  "#05C3DE",
  "#654EA3",
]

function EventsArea() {

  const {activeAbilities, fightData} = useReportContext()
  const [imageUrl, setImageUrl] = useState()

  useEffect(() => {
    async function onPaste(event) {
      const {code, ctrlKey} = event
      if (code === "KeyV" && ctrlKey) {
        try {
          const items = await navigator.clipboard.read()
          const imageBlob = await items[0].getType("image/png")
          const reader = new FileReader()
          reader.readAsDataURL(imageBlob)
          reader.onload = function() {
            const imageBase64Url = reader.result
            setImageUrl(imageBase64Url)
          }
        }
        catch (error) {
          console.error(error)
        }
      }
    }

    window.addEventListener("keydown", onPaste)

    return () => window.removeEventListener("keydown", onPaste)
  }, [])

  return (
    <S.Root>
      {imageUrl && (
        <S.BackgroundImage src={imageUrl} alt="image" />
      )}
      {!!activeAbilities && Object.entries(activeAbilities).map(([enemyId, casts], index) => {
        return (
          <S.Enemy $color={colors[index % colors.length]}>
            <S.Title>{fightData?.enemies?.[enemyId]?.name}</S.Title>
            {casts.map(abilityId => {
              return (
                <EventBar key={abilityId} ability={fightData?.abilities?.[abilityId]} timestamps={fightData?.casts?.[enemyId]?.[abilityId]} color={colors[index % colors.length]} />
              )
            })}
          </S.Enemy>
        )
      })}
    </S.Root>
  )
}

export default EventsArea