import { useEffect, useState } from "react";
import _throttle from "lodash/throttle";
import { getInnerMousePosition } from "../utils";


const useMouseMove = (element) => {

  const [[xPos, yPos], setPosition] = useState(['-', '-'])

  useEffect(() => {
    if (!element) return

    const handleMouseMove = (e) => {
      setPosition(getInnerMousePosition(element, e))
    }

    const throttledHandleMouseMove = _throttle(handleMouseMove, 20)
    element.addEventListener('mousemove', throttledHandleMouseMove)

    return () => {
      if (!element) return
      element.removeEventListener("mousemove", throttledHandleMouseMove);
    };
  }, [element]);

  return [xPos, yPos]
};


export default useMouseMove