import useReportContext from "context/reportContext"
import { useMemo } from "react"
import * as S from './styled'

const EventBar = ({
  ability,
  timestamps,
  color,
}) => {

  const {fightData = {}} = useReportContext()
  const {duration, start} = fightData

  const AbilityInstances = useMemo(() => {
    return timestamps.map((instanceTime, i) => {

      instanceTime = instanceTime - start

      const percentLeft = (instanceTime / 1000) / (duration / 1000) * 100

      return (
        <S.InstancePositioner
          key={i}
          style={{
            '--left-pos': percentLeft + '%'
          }}
        >
          <S.AbilityMarker />
        </S.InstancePositioner>
      )
    })
  }, [timestamps, start, duration])

  return (
    <S.Root $color={color}>
      <S.Title>{ability.name}</S.Title>
      <S.DummySizer>
        <S.AbilityMarker />
      </S.DummySizer>
      {AbilityInstances}
    </S.Root>
  )
}


export default EventBar