import qs from 'qs'

const BASE_URL = "https://www.warcraftlogs.com:443/v1/"
const WARCRAFTLOGS_API_KEY = "a5de10f27b70d3c107a68397738023df"

export async function get(url, params) {
  const response = await fetch(`${BASE_URL}${url}?${qs.stringify({
    api_key: WARCRAFTLOGS_API_KEY,
    ...params,
  })}`)
  return response.json()
}