import { useForm } from 'react-hook-form'
import { useCallback, useMemo, useEffect } from 'react'
import useReportContext from 'context/reportContext'

function FightForm() {

  const {register, watch, setValue} = useForm()
  const [report, fight, casts] = watch(["report", "fight", "casts"])

  useEffect(() => {
    setValue("casts", [])
  }, [fight, setValue])

  const {setReport, setFight, fightsList, fightData, showCasts} = useReportContext()

  useEffect(() => {
    showCasts(casts)
  }, [casts, showCasts])

  const fetchReport = useCallback(() => {
    setReport(report)
  }, [setReport, report])

  const fetchEnemyEventData = useCallback(() => {
    setFight(fight)
  }, [setFight, fight])

  // Rendering
  const fightSelect = useMemo(() => {
    if (!fightsList) return false
    return (
      <>
      <select {...register("fight")}>
        {fightsList.map(fight => {
          const {id, kill, name, boss, difficulty} = fight
          if (!boss) return false
          return (
            <option key={id} value={id}>{`${difficulty}-${name}${kill ? " (kill)" : ""}`}</option>
          )
        })}
      </select>
      <button onClick={fetchEnemyEventData}>go</button>
      </>
    )
  }, [fightsList, register, fetchEnemyEventData])

  const castsSelect = useMemo(() => {
    if (!fightData) return false

    return (
      Object.entries(fightData.casts).map(([enemyId, abilities]) => {

        const enemy = fightData.enemies[enemyId]

        if (enemyId === "other") {
          return <div key={enemyId}>{enemy.name}</div>
        }

        return (
          <div key={enemyId}>
            <div style={{color: "white", fontSize: "1.4rem"}}>{enemy.name}</div>
            <div>
              {Object.keys(abilities).map((abilityId) => {
                const {name, guid} = fightData.abilities[abilityId]
                return (
                  <label key={guid} style={{display: "block", color: "white"}}>
                    <input type="checkbox" {...register("casts")} value={`${enemyId}-${abilityId}`} />
                    {name}
                  </label>
                )
              })}
            </div>
          </div>
        )
      })
    )
  }, [fightData, register])

  return (
    <div>
      <div style={{color: "white"}}>WjptDNZ2dnq6Lg1v</div>
      <input {...register("report")} />
      <button onClick={fetchReport}>go</button>
      {fightSelect}
      {castsSelect}
    </div>
  )
}

export default FightForm