import classAbilities from "config/data/classAbilities"
import players from "config/data/players"
import { createContext, useContext, useMemo } from "react"
import { useState, useCallback } from "react"
import _cloneDeep from 'lodash/cloneDeep'
import useGetCurrentValue from "hooks/useGetCurrentValue"

const PlayerContext = createContext()

const initialPlayers = players.map(({name, playerClass}) => ({
  name,
  playerClass,
  abilities: _cloneDeep(classAbilities[playerClass])
}))

export function PlayerContextProvider({children}) {

  const [players, setPlayers] = useState(initialPlayers)
  const getPlayers = useGetCurrentValue(players)

  const [activeAbilities, setActiveAbilities] = useState({})
  const getActiveAbilities = useGetCurrentValue(activeAbilities)

  const getPlayerData = useCallback((playerName) => {
    const currentPlayers = getPlayers()
    return currentPlayers.find(player => player.name === playerName)
  }, [getPlayers])

  const getAbilityData = useCallback((playerName, abilityName) => {
    const playerData = getPlayerData(playerName)
    return playerData.abilities.find(ability => ability.name === abilityName)
  }, [getPlayerData])

  const isPlayerAbilityActive = useCallback((playerName, abilityName) => {
    const currentActiveAbilities = getActiveAbilities()
    return currentActiveAbilities[playerName]?.includes(abilityName)
  }, [getActiveAbilities])

  const toggleAbility = useCallback((playerName, abilityName) => {
    setActiveAbilities(prevState => {
      const newState = _cloneDeep(prevState)
      const playerData = newState[playerName]
      if (!playerData) {
        newState[playerName] = [abilityName]
      }
      else {
        if (playerData.includes(abilityName)) {
          newState[playerName] = playerData.filter(name => name !== abilityName)
        }
        else {
          newState[playerName].push(abilityName)
        }
      }
      return newState
    })
  }, [])

  const activePlayers = useMemo(() => {
    return Object.keys(activeAbilities).filter(
      playerName => !!activeAbilities[playerName].length
    )
  }, [activeAbilities])

  const contextValue = useMemo(() => {
    return {
      players,
      activePlayers,
      activeAbilities,
      toggleAbility,
      getPlayerData,
      getAbilityData,
      isPlayerAbilityActive,
    }
  }, [
    players,
    activePlayers,
    activeAbilities,
    toggleAbility,
    getPlayerData,
    getAbilityData,
    isPlayerAbilityActive
  ])

  return (
    <PlayerContext.Provider value={contextValue}>
      {children}
    </PlayerContext.Provider>
  )
}

function usePlayerContext() {
  const playerContext = useContext(PlayerContext)
  return playerContext
}

export default usePlayerContext