import * as S from './styled'
import ClassPanel from '../ClassPanel'
import usePlayerContext from 'context/playerContext'
import usePanelContext from 'context/panelContext'
import useReportContext from 'context/reportContext'

const AbilitiesArea = () => {

  const {width} = usePanelContext()
  const {fightData = {}} = useReportContext()
  const {duration} = fightData
  const {activePlayers, activeAbilities, getPlayerData, getAbilityData} = usePlayerContext()

  return (
    <S.Root>
      {activePlayers.map(playerName => (
        <ClassPanel
          key={playerName}
          player={getPlayerData(playerName)}
          activeAbilities={activeAbilities[playerName].map(abilityName => getAbilityData(playerName, abilityName))}
          width={width}
          totalTime={duration / 1000}
        />
      ))}
    </S.Root>
  )
}

export default AbilitiesArea