import * as S from './styled'
import AbilityBar from '../AbilityBar'

const ClassPanel = ({
  player,
  activeAbilities,
  width,
  totalTime,
}) => {
  const {name, playerClass} = player

  return (
    <S.Root sPlayerClass={playerClass}>
      <S.InfoBar>
        {name}
      </S.InfoBar>
      <S.AbilityPanel>
        {activeAbilities.map(ability => (
          <AbilityBar
            key={ability.name}
            panelWidth={width}
            ability={ability}
            totalTime={totalTime}
          />
        ))}
      </S.AbilityPanel>
    </S.Root>
  )
}


export default ClassPanel