/* eslint-disable react-hooks/exhaustive-deps */
import { getFightEnemyEvents, getReportFights } from 'api'
import {useContext, createContext, useMemo, useCallback, useState, useEffect} from 'react'
import { useQuery } from 'react-query'

const ReportContext = createContext()

export function ReportContextProvider({children}) {

  // Report data
  const [reportId, setReportId] = useState(null)

  const {
    data: reportData,
    refetch: reportDataRefetch,
  } = useQuery(["reports", reportId], () => getReportFights(reportId), {
    refetchOnWindowFocus: false,
    enabled: false,
  })

  useEffect(() => {
    if (reportId) {
      reportDataRefetch()
    }
  }, [reportId])

  const setReport = useCallback((newReportId) => {
    setReportId(newReportId)
  }, [])

  // Fight data
  const [fightId, setFightId] = useState(null)
  const [customDuration, setCustomDuration] = useState(0)

  const setFight = useCallback((newFightId) => {
    setFightId(newFightId)
  }, [])

  // Enemy events data
  const {
    data: enemyEventsData,
    refetch: enemyEventsDataRefetch,
  } = useQuery(
    ["enemy-events", reportId, fightId],
    () => getFightEnemyEvents(reportId, reportData?.fights?.find(fightData => fightData.id === parseInt(fightId))),
    {
      refetchOnWindowFocus: false,
      enabled: false,
    }
  )

  useEffect(() => {
    if (reportId && fightId) {
      enemyEventsDataRefetch()
    }
  }, [reportId, fightId])

  /* Fight data
    const fightData = {
      duration,
      enemies: {
        [enemyId] {
          ...enemy,
        },
      },
      abilities: {
        [abilityId]: {
          ...ability,
        },
      },
      casts: {
        [enemyId]: {
          [abilityId]: [timestamp1, timestamp2...],
        },
      },
    }
  */
  const fightData = useMemo(() => {

    const newFightData = {
      enemies: {},
      abilities: {},
      casts: {},
      duration: customDuration,
      start: 0,
    }

    if (reportData && enemyEventsData) {

      const fight = reportData.fights.find(fightData => fightData.id === parseInt(fightId))
      newFightData.duration = fight.end_time - fight.start_time
      newFightData.start = fight.start_time

      enemyEventsData.events.forEach((event) => {
        if (event.type !== "cast") return

        const {sourceID = "other", ability, timestamp} = event
        const {guid} = ability
        const enemy = reportData.enemies.find(currentEnemy => currentEnemy.id === sourceID) || {name: sourceID, id: sourceID}

        if (!newFightData.abilities[guid]) newFightData.abilities[guid] = {...ability}
        if (!newFightData.enemies[sourceID]) newFightData.enemies[sourceID] = {...enemy}

        const castTimestamps = newFightData.casts?.[sourceID]?.[guid]
        if (!castTimestamps) {
          if (!newFightData.casts[sourceID]) {
            newFightData.casts[sourceID] = {
              [guid]: [timestamp]
            }
          }
          else {
            newFightData.casts[sourceID][guid] = [timestamp]
          }
        }
        else castTimestamps.push(timestamp)
      })

    }

    return newFightData

  }, [reportData, enemyEventsData, customDuration])

  // Active casts data
  const [activeAbilities, setActiveAbilities] = useState(null)

  const showCasts = useCallback((newCasts) => { // Format: "enemyId-abilityId"
    if (!newCasts) return
    const newActiveAbilities = {}
    newCasts.forEach(castString => {
      const [enemyId, abilityId] = castString.split("-")
      if (!newActiveAbilities[enemyId]) {
        newActiveAbilities[enemyId] = [abilityId]
      }
      else {
        newActiveAbilities[enemyId].push(abilityId)
      }
    })
    setActiveAbilities(newActiveAbilities)
  }, [])

  const contextValue = useMemo(() => {
    return {
      setReport,
      setFight,
      fightsList: reportData?.fights,
      fightData,
      showCasts,
      activeAbilities,
      setCustomDuration,
    }
  }, [setReport, setFight, reportData, fightData, showCasts, activeAbilities])

  console.log({reportData, enemyEventsData, fightData, activeAbilities})

  return (
    <ReportContext.Provider value={contextValue}>
      {children}
    </ReportContext.Provider>
  )
}

function useReportContext() {
  const reportContext = useContext(ReportContext)
  return reportContext
}

export default useReportContext