import styled from "styled-components/macro";
import { generateCSSDataObject, getCSS } from "../../../config/theme/utils";

export const Container = styled.div`
  ${({ theme, sWithoutPadding }) =>
    !sWithoutPadding &&
    getCSS(
      generateCSSDataObject({
        "padding-left": theme.pagePadding,
        "padding-right": theme.pagePadding,
      })
    )}
`;

export const Content = styled.div`
  max-width: ${({ theme, sWidth }) => theme.contentWidth[sWidth || "default"]};
  margin: 0 auto;
`;
