import {get} from './HTTPClient'

export async function getReportFights(report) {
  return await get(`report/fights/${report}`)
}

export async function getFightEnemyEvents(report, fight) {
  return await get(`report/events/casts/${report}`, {
    hostility: 1,
    start: fight.start_time,
    end: fight.end_time,
  })
}