import usePlayerContext from "context/playerContext"
import Ability from "../Ability"
import {useCallback} from 'react'
import * as S from './styled'

const Player = ({player}) => {
  const {
    toggleAbility,
    isPlayerAbilityActive,
  } = usePlayerContext()
  const {name, playerClass, abilities} = player

  const handleAbilityClick = useCallback((abilityName) => {
    toggleAbility(name, abilityName)
  }, [name, toggleAbility])

  return (
    <S.Root $playerClass={playerClass}>
      <S.PlayerName>
        {name}
      </S.PlayerName>
      <S.PlayerAbilities>
        {abilities.map(ability => (
          <Ability
            key={ability.name}
            ability={ability}
            active={isPlayerAbilityActive(name, ability.name)}
            onClick={() => handleAbilityClick(ability.name)}
          />
        ))}
      </S.PlayerAbilities>
    </S.Root>

  )
}

export default Player